<template>
    <div class="title">
      <span>Admin</span>
      <div class="hr"></div>
      <router-view v-if="show"/>
    </div>
</template>

<script>
  import UserService from '../services/user.service';

  export default {
    name: 'Admin',
    data() {
      return {
        content: '',
        show: false
      };
    },
    mounted() {
      UserService.getAdminBoard().then(
          response => {
            this.show = true;
            this.content = response.data;
          },
          error => {
            this.show = false;
            this.content =
                (error.response && error.response.data && error.response.data.message) ||
                error.message ||
                error.toString();
          }
      );
    }
  };
</script>
<style>
  @import '../assets/css/main.css';
</style>